/* eslint-disable */
import React, { useState } from 'react';
import { Navbar,Container,Nav,NavDropdown,Button,FormControl,Form,Offcanvas } from 'react-bootstrap';
import './App.css';
import Data from './data.js';
import Detail from './Detail.js';
import { Link, Route, Switch, useHistory } from 'react-router-dom';
import axios from 'axios';

function App() {
  let [shoes, shoes변경] = useState(Data);
  let[재고, 재고변경] = useState([13,11,12,4,6,1,5,7,8,2,5,23,24,7]);
  return (
    <div className="App">
      <Navbar bg="light" expand={false}>
      <Container fluid>
        <Navbar.Brand><Link className='text-link' to="/">Shoes</Link></Navbar.Brand>
        {/* <Nav.Link as={Link} to="/detail">Detail</Nav.Link> */}
        <Navbar.Toggle aria-controls="offcanvasNavbar" />
        <Navbar.Offcanvas
        id="offcanvasNavbar"
        aria-labelledby="offcanvasNavbarLabel"
        placement="end">
        <Offcanvas.Header closeButton>
          <Offcanvas.Title id="offcanvasNavbarLabel">Offcanvas</Offcanvas.Title>
        </Offcanvas.Header>
        <Offcanvas.Body>
          <Nav className="justify-content-end flex-grow-1 pe-3">
            <Nav.Link as={Link} to="/">Home</Nav.Link>
            <Nav.Link href="#action2">Link</Nav.Link>
            <NavDropdown title="Dropdown" id="offcanvasNavbarDropdown">
              <NavDropdown.Item href="#action3">Action</NavDropdown.Item>
              <NavDropdown.Item href="#action4">Another action</NavDropdown.Item>
            <NavDropdown.Divider />
              <NavDropdown.Item href="#action5">
                Something else here
              </NavDropdown.Item>
            </NavDropdown>
          </Nav>
          <Form className="d-flex">
            <FormControl
              type="search"
              placeholder="Search"
              className="me-2"
              aria-label="Search"/>
              <Button variant="outline-success">Search</Button>
          </Form>
        </Offcanvas.Body>
        </Navbar.Offcanvas>
      </Container>
      </Navbar>
    <Switch>
    <Route exact path="/">
    <div className="background">
        <div className='textBox'>
        <h1>shoes 20% Season Off</h1>
        <p></p>
        <p><Button variant="secondary">Learn More</Button></p>
        </div>
      </div>
      <div className="container">
        <div className="row">
        {
          shoes.map((a,i)=>{
            return <Card shoes={shoes[i]} i={i} key={i}/>
          })
        }
        </div>
        <button className='btn btn-primary' onClick={()=>{
          axios.get('https://codingapple1.github.io/shop/data2.json')
          .then((result)=>{ 
            console.log(result.data);
            shoes변경([...shoes, ...result.data]);
           })
          .catch(()=>{ 
            console.log('실패했어요')
           })
        }}>more</button>
      </div>
    </Route>
    <Route path="/detail/:id">
        <Detail shoes={shoes} 재고={재고} 재고변경={재고변경}/>
    </Route>
    </Switch>
    </div>
  );
}
function Card(props){
  let history = useHistory();
  return(
    <div className="col-md-4 item" onClick={ ()=>{ history.push('/detail/' + props.shoes.id) } }>
      <img src={require('./images/item' + (props.shoes.id)+ '.jpg')} alt='' width="100%" />
      <h4>{ props.shoes.title }</h4>
      <p>{ props.shoes.content}</p>
      <small>{ props.shoes.price}₩</small>
    </div>
  )
}

export default App;
